import React, { Component } from 'react';
import { Card, CardBody, CardHeader, Col, Row, Button } from 'reactstrap';
import feathers from '../../feathers';

import BootstrapTable from 'react-bootstrap-table-next';

class TipoUsuarios extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: []
    };
  }

  async componentWillMount() {
    var _this = this;

    let typesResult = await feathers.service('types').find({});

    console.log('typesResult', typesResult);

    this.setState({
      data: typesResult.data,
      total: typesResult.total
    });
  }

  render() {
    var indexNumber = 0;

    const columns = [
      {
        dataField: 'nombre',
        text: 'Nombre',
        sort: true,
        formatter: (cell, row) => {
          return cell;
        }
      },

      {
        dataField: 'actions',
        text: 'Acciones',
        headerStyle: (colum, colIndex) => {
          return { width: 150 };
        },
        formatter: (cell, row) => {
          return (
            <Row>
              <Col col="6" sm="4" md="2" xl className="mb-3 mb-xl-0">
                <Button
                  color="link"
                  onClick={() => this.props.history.push('/tipos/' + row._id)}
                >
                  {/* //DONE Paty - Hacer que te env√≠e a Editar */}
                  Editar
                </Button>
                {'   '}
              </Col>
            </Row>
          );
        }
      }
    ];

    return (
      <div className="animated fadeIn">
        <Row>
          <Col xs="12" lg="12">
            <Card>
              <Row
                className="pull-right"
                style={{
                  marginTop: '5px',
                  paddingRight: '5px'
                }}
              >
                <Col col="12" sm="12" md="12">
                  <Button
                    color="success"
                    onClick={() => this.props.history.push('/tipos/form')}
                    className="pull-right"
                  >
                    Agregar Tipo de usuario
                  </Button>
                  {'   '}
                  {/* <Button
                  color="success"
                  onClick={() => {
                    this.sendHandled(row.companyId);
                  }}
                >
                  Enviado
                </Button> */}
                </Col>
              </Row>
              <Row>
                {' '}
                <CardBody>
                  <BootstrapTable
                    keyField="_id"
                    data={this.state.data}
                    columns={columns}
                    bootstrap4
                    striped
                    hover
                  />
                </CardBody>
              </Row>
            </Card>
          </Col>
        </Row>
      </div>
    );
  }
}

export default TipoUsuarios;
